import Base from '../Base';
import Adapter from 'enzyme-adapter-react-16';
import {mount, shallow} from 'enzyme';
import * as enzyme from 'enzyme';
import {useNavigation} from '@react-navigation/native';
import {NativeStackNavigationProp} from '@react-navigation/native-stack/lib/typescript/src/types';
import {TextInput, Button} from 'react-native';
import {QuizContext} from './../QuizContext';
enzyme.configure({adapter: new Adapter()});
const navigation = useNavigation<NativeStackNavigationProp<any>>();
jest.mock('@react-navigation/native', () => {
  return {
    useNavigation: jest.fn().mockReturnValue({
      navigate: jest.fn(),
    }),
  };
});

const mockContext = {
  name: 'chandrakanth',
  email: 'chandu@kanth.com',
  phone: '1234567890',
  score: 0,
  trackScore: true,
  selectedValue: 'ReactJS',
  setName: jest.fn(),
  setMail: jest.fn(),
  setPhone: jest.fn(),
  setSelected: jest.fn(),
  setTracker: jest.fn(),
  setScore: jest.fn(),
};

describe('should render a form with three text inputs, two selections, and one submit', () => {
  let wrapper: any;

  beforeEach(() => {
    wrapper = shallow(
      <QuizContext.Provider value={mockContext}>
        <Base />
      </QuizContext.Provider>,
    );
  });
  it('component renders correctly', () => {
    expect(wrapper).toMatchSnapshot();
  });
  it('should render a form with three text inputs, two selections, and one submit button', () => {
    expect(wrapper.find('TextInput')).toHaveLength(3);
    expect(wrapper.find('SelectDropdown')).toHaveLength(2);
    expect(wrapper.find('Button')).toHaveLength(2);
  });
  it('should disable the submit button when the form is invalid', () => {
    expect(wrapper.find('Button').at(0).prop('disabled')).toBe(true);
  });
  it('should enable the submit when all the inputs are entered', () => {
    const nameInput = wrapper.find(TextInput).at(0);
    const emailInput = wrapper.find(TextInput).at(1);
    const phoneInput = wrapper.find(TextInput).at(2);

    nameInput.simulate('changeText', 'Chandrakanth');
    emailInput.simulate('changeText', 'chandu@cc.com');
    phoneInput.simulate('changeText', '0909090909');

    expect(wrapper.find(Button).at(0).prop('disabled')).toBe(false);
  });
});
